import React from 'react'
import Peer from 'peerjs'

function genCode(length=5) {
	var result           = '';
	var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
	var charactersLength = characters.length;
	for ( var i = 0; i < length; i++ ) {
		result += characters.charAt(Math.floor(Math.random() * 
		charactersLength));
	}
	return result;
}

function capitalizeTheFirstLetterOfEachWord(words) {
	var separateWord = words.toLowerCase().split(' ');
	for (var i = 0; i < separateWord.length; i++) {
		separateWord[i] = separateWord[i].charAt(0).toUpperCase() +
		separateWord[i].substring(1);
	}
	return separateWord.join(' ');
}

const NameInput = props => {
	const [name, setName] = React.useState('');
	const [errors, setErrors] = React.useState({});

	const onChange = e => {
		setName(e.target.value);
	}

	const onSubmit = e => {
		e.preventDefault();

		if(name.match(/^[A-zÀ-ú]+$/))
			props.onValid(name);
		else
			setErrors({name: 'Il faut choisir un nom avec des lettres.'});
	};

	React.useEffect(() => {
		document.getElementById('name').focus();
	}, []);

	return <form onSubmit={onSubmit}>
		<div className="mb-3">
			<label htmlFor="name" className="form-label">T'es qui ?</label>
			<div className="input-group has-validation">
				<input type="text" className={"form-control"+(errors.name ? ' is-invalid':'')} value={name} onChange={onChange} id="name"/>
				{errors.name && <div id="validationServerUsernameFeedback" className="invalid-feedback">
					{errors.name}
				</div>}
			</div>
		</div>
		<button type="submit" className="btn btn-primary">Valider</button>
	</form>
}

const FilmForm = props => {
	const [name, setName] = React.useState('');
	const [platforms, setPlatforms] = React.useState([]);
	const [errors, setErrors] = React.useState({});

	const onChangeName = e => {
		setName(e.target.value);
	}

	const onChangePlatforms = e => {
		let v = [...e.target.selectedOptions].map(o => o.value);
		setPlatforms(v);
	}

	const onSubmit = e => {
		e.preventDefault();

		let errors = {};
		if(name.length === 0)
			errors.name = "Faut choisir le nom d'un film !";
		if(platforms.length === 0)
			errors.platforms = "Faut choisir au moins une plateforme !";

		setErrors(errors);

		if(Object.keys(errors).length === 0) {
			props.onAjouter({name: capitalizeTheFirstLetterOfEachWord(name), platforms});
			setName('');
			setPlatforms([]);
		}
	};

	return <tr>
		<td>
			<div className="input-group has-validation">
				<input type="text" className={"form-control"+(errors.name ? ' is-invalid':'')} value={name} onChange={onChangeName} id="name" placeholder="Nom du film"/>
				{errors.name && <div id="validationServerUsernameFeedback" className="invalid-feedback">
					{errors.name}
				</div>}
			</div>
		</td>
		<td>
			<div className="input-group has-validation">
				<select multiple value={platforms} onChange={onChangePlatforms} className={"form-select"+(errors.platforms ? ' is-invalid':'')}>
					<option>Netflix</option>
					<option>Amazon</option>
					<option>Disney</option>
					<option>OCS</option>
					<option>Salto</option>
					<option>Canal+</option>
					<option>Apple TV</option>
				</select>
				{errors.platforms && <div id="validationServerUsernameFeedback" className="invalid-feedback">
					{errors.platforms}
				</div>}
			</div>
		</td>
		<td><button type="button" className="btn btn-primary" onClick={onSubmit}><i className="bi bi-plus-circle-fill"></i></button></td>
	</tr>
}

const SalonForm = props => {
	const [join, setJoin] = React.useState(false);
	const [name, setName] = React.useState('');
	const [errors, setErrors] = React.useState({});
	const [loading, setLoading] = React.useState(false);

	if(join) {
		const onChangeName = e => {
			setName(e.target.value);
		}

		const onSubmit = e => {
			e.preventDefault();

			let errors = {};
			if(name.length === 0)
				errors.name = "Il faut indiquer le code du salon !";

			setErrors(errors);

			if(Object.values(errors).length === 0) {
				setLoading(true);
				props.onRejoindre(name.trim().toUpperCase())
					// .then(() => {
					// 	// console.log('!')
					// 	setLoading(false);
					// 	setName('');
					// 	setJoin(false);
					// })
					.catch(e => {
						// console.log(e)
						setLoading(false);
						if(e.message.includes('is taken'))
							setErrors({name: "Nom déjà utilisé."});
						else
							setErrors({name: 'Impossible de rejoindre ce salon'});
					})
					;
			}
		};

		if(loading) {
			return <div className="spinner-border" role="status">
				<span className="visually-hidden">Loading...</span>
			</div>
		}

		return <form onSubmit={onSubmit}>
			<div className="mb-3">
				<div className="input-group has-validation">
					<input type="text" className={"form-control"+(errors.name ? ' is-invalid':'')} value={name} onChange={onChangeName} id="name" placeholder="Code du salon"/>
					{errors.name && <div id="validationServerUsernameFeedback" className="invalid-feedback">
						{errors.name}
					</div>}
				</div>
			</div>
			<button type="submit" className="btn btn-primary">Rejoindre</button>
		</form>
	}

	return <>
		<p>Créer ou rejoindre un salon ?</p>
		<div className="buttons">
			<button className="btn btn-primary" type="button" onClick={props.onCreer}>créer</button>
			<button className="btn btn-secondary" type="button" onClick={() => setJoin(true)}>rejoindre</button>
		</div>
	</>
}

const Content = props => {
	const [name, setName] = React.useState(null);
	// const [name, setName] = React.useState('bob');
	const [salon, setSalon] = React.useState(null);
	const [films, setFilms] = React.useState([]);
	// const [films, setFilms] = React.useState([{name: 'b', platforms: ['Netflix']}]);
	const [autres, setAutres] = React.useState({});
	// const [autres, setAutres] = React.useState({Michel: [{name: 'a', platforms: ['Netflix']}]});

	const [winner, setWinner] = React.useState(null);

	const peerRef = React.useRef();
	// const connsRef = React.useRef({});
	// const connRef = React.useRef(null);
	// const autresRef = React.useRef({});

	// const setAutres = v => {
	// 	autresRef.current = v;
	// 	_setAutres(v);
	// };

	const nameRef = React.useRef();
	nameRef.current = name;
	const filmsRef = React.useRef();
	filmsRef.current = films;
	const autresRef = React.useRef();
	autresRef.current = autres;

	// return <>
	// 	<div id="webkit-keyframes-animation">Je me réduis</div>
	// </>

	// React.useEffect(() => {
	// 	setWinner({
	// 		name: 'Blabla',
	// 		platforms: ['Netflix', 'Amazon']
	// 	});
	// }, []);

	if(winner) {
		return <>
			<div className="countdown">
				<div className="num5">5</div>
				<div className="num4">4</div>
				<div className="num3">3</div>
				<div className="num2">2</div>
				<div className="num1">1</div>
				<div className="num0">
					<h1>{winner.name}</h1>
					{winner.platforms.join(', ')}
				</div>
			</div>
		</>
	}

	if(name === null) {
		return <NameInput onValid={name => {
			setName(name);
		}}/>
	}

	const syncToOne = (autres, autre) => {
		// let conn = connsRef.current[autre];
		// console.log(peerRef.current.connections)
		// console.log(Object.keys(peerRef.current.connections))
		// console.log(autre)
		// console.log(peerRef.current.connections['tirage-membre-'+autre])
		let conn = peerRef.current.connections[autre][0];
		conn.send({
			type: 'sync',
			autres: autres,
		});
	};

	const syncToAll = (autres, except=null) => {
		// for(let autre in connsRef.current) {
			// if(autre === except)
		for(let autre in peerRef.current.connections) {
			if(autre === 'tirage-membre-'+except)
				continue;
			syncToOne(autres, autre);
		}
	};

	const showWinner = winner => {
		// console.log(winner)
		setWinner(winner);
		// TODO
	};

	const onCreerSalon = () => {
		let code = genCode();

		const peer = new Peer('tirage-salon-'+code, {
			secure:true,
			host: 'my-peerjs-hog.herokuapp.com',
			port: 443,
			debug: 3
		});
		peerRef.current = peer;
		peer.on('connection', (conn) => {
			let connName;

			conn.on('data', (data) => {
				console.log('DATA!')
				console.log(data)
				if(data.type === 'hello') {
					connName = data.name;
					// connsRef.current[data.name] = conn;

					syncToOne(Object.assign({}, autresRef.current, {[nameRef.current]: filmsRef.current}), 'tirage-membre-'+connName);

					let copy = Object.assign({}, autresRef.current, {[connName]: []});
					setAutres(copy);
				}
				else if(data.type === 'add') {
					// console.log(autresRef.current)
					let copy = Object.assign({}, autresRef.current);
					copy[connName] = copy[connName].slice();
					copy[connName].push(data.film);

					syncToAll(Object.assign({}, copy, {[nameRef.current]: filmsRef.current}), connName);

					setAutres(copy);
				}
				else if(data.type === 'remove') {
					let copy = Object.assign({}, autresRef.current);
					copy[connName] = copy[connName].slice();
					copy[connName].splice(data.index, 1);

					syncToAll(Object.assign({}, copy, {[nameRef.current]: filmsRef.current}), connName);

					setAutres(copy);
				}
			});
			conn.on('close', () => {
				console.log('CLOSE: '+connName)
				let copy = Object.assign({}, autresRef.current);
				delete copy[connName];

				syncToAll(Object.assign({}, copy, {[nameRef.current]: filmsRef.current}), connName);

				setAutres(copy);
			});
			// conn.on('open', () => {
			// 	console.log('salon:open')
			// 	conn.send('hello!');
			// });
		});

		setSalon({code, created: true});
	};

	const onRejoindreSalon = code => {
		return new Promise((resolve, reject) => {
			const peer = new Peer('tirage-membre-'+name, {
				secure:true,
				host: 'my-peerjs-hog.herokuapp.com',
				port: 443,
				debug: 3
			});
			peerRef.current = peer;
			peer.on('error', err => {
				console.log('ERROR')
				console.log(err)
				reject(err);
			});
			peer.on('open', () => {
				const conn = peer.connect('tirage-salon-'+code, {
					// reliable: true
				});
				// connRef.current = conn;
				conn.on('open', () => {
					// console.log('open')
					conn.send({
						type: 'hello',
						name,
					});
					setSalon({code, created: false});
					setFilms([]);
					setAutres({});
					resolve();
				});
				conn.on('data', data => {
					console.log('DATA!')
					console.log(data)
					if(data.type === 'sync') {
						delete data.autres[name];
						setAutres(data.autres);
					}
					else if(data.type === 'winner') {
						showWinner(data.winner);
					}
				});
				conn.on('error', err => {
					console.log('ERROR')
					console.log(err)
					reject(err);
				});
				conn.on('close', () => {
					console.log('close')
					setSalon(null);
				});
			});
		});
	};

	const supprimer = i => {
		let copy = films.slice();
		copy.splice(i, 1);
		setFilms(copy);

		if(salon.created)
			syncToAll(Object.assign({}, autresRef.current, {[name]: copy}));
		else {
			// connRef.current.send({
			Object.values(peerRef.current.connections)[0][0].send({
				type: 'remove',
				index: i,
			});
		}
	};
	const ajouter = film => {
		let copy = films.slice();
		copy.push(film);
		setFilms(copy);

		if(salon.created)
			syncToAll(Object.assign({}, autresRef.current, {[name]: copy}));
		else {
			// connRef.current.send({
			// console.log(peerRef.current.connections)
			Object.values(peerRef.current.connections)[0][0].send({
				type: 'add',
				film,
			});
		}
	};

	const onQuitterSalon = () => {
		peerRef.current.destroy();
		setSalon(null);
		setFilms([]);
		setAutres({});
	};

	const onTirageAuSort = () => {
		let autresArr = Object.values(autresRef.current)
		let all = 
			autresArr.length > 0 ?
				autresArr.reduce((acc, v) => {
					let r = acc ?? [];
					for(let f of v)
						r.push(f); 
					return r;
				})
				:
				[]
		;
		for(let f of films)
			all.push(f);
		// console.log(all);

		let winner = all[Math.floor(Math.random() * all.length)];
		// console.log(winner)

		// for(let autre in connsRef.current) {
		// 	let conn = connsRef.current[autre];
		for(let autre in peerRef.current.connections) {
			let conn = peerRef.current.connections[autre][0];
			conn.send({
				type: 'winner',
				winner,
			});
		}

		showWinner(winner);
	};

	let ready = salon?.created && films.length === 3;
	if(ready) {
		for(let autre of Object.values(autres)) {
			if(autre.length < 3) {
				ready = false;
				break;
			}
		}
	}

	return <>
		<h1>Salut {name} !</h1>
		<hr/>
		{
			!salon ?
				<SalonForm onCreer={onCreerSalon} onRejoindre={onRejoindreSalon}/>
				:
				<>
					<div>
						<p>Tu as {salon.created ? 'créé':'rejoint'} le salon : {salon.code}</p>
						<div className="buttons">
							{salon.created && <button disabled={!ready} type="submit" className="btn btn-success" onClick={onTirageAuSort}>Tirer au sort</button>}
							<button type="submit" className="btn btn-danger" onClick={onQuitterSalon}>Quitter</button>
						</div>
					</div>
					<hr/>
					<div>
						<h2>Mes films</h2>
						<table className="table">
							<thead>
								<tr>
									<th scope="col">Nom</th>
									<th scope="col">Plateformes</th>
									<th scope="col"></th>
								</tr>
							</thead>
							<tbody>
								{films.map((f, i) => (<tr key={f.name}>
									<td>{f.name}</td>
									<td>{f.platforms.join(', ')}</td>
									<td><button type="button" className="btn btn-danger" onClick={() => supprimer(i)}><i className="bi bi-trash-fill"></i></button></td>
								</tr>))}
								{films.length < 3 && <FilmForm onAjouter={ajouter}/>}
							</tbody>
						</table>
					</div>
					{Object.values(autres).length > 0 && <>
						<hr/>
						<h2>Les autres participants</h2>
						<table className="table">
							<tbody>
								{Object.entries(autres).map(([name, films]) => <tr key={name}>
									<td>{name}</td>
									<td>{films.length} film{films.length > 1 && 's'}</td>
								</tr>)}
							</tbody>
						</table>
					</>}
				</>
		}
	</>
}

const App = props => {
	return <div className="container">
		<Content/>
	</div>
}

export default App